.background {
    background-image: linear-gradient(rgb(16, 73, 8), rgb(86, 112, 16));
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    .about-form {
        height: 85%;
        width: 85%;
        text-align: center;
        background-color: #d8d2b8;
        border-radius: 5px;
        font-weight: bold;
    }
}

.header {
    text-align: center;
    font-size: 3rem;
    letter-spacing: 10px;
    padding: 15px;
}

.content {
    text-align: center;
    font-size: 1.2rem;
    letter-spacing: 3px;
    padding: 10px;
}

.dropdown-wrapper {
    margin: 0 auto;
}

.dropdown-wrapper ul {
    list-style: none;
    margin-top: 1%;
}

.dropdown-wrapper ul li {
    background: black;
    width: 170px;
    font-size: 1rem;
    color: white;
    line-height: 50px;
    text-align: center;
    float: left;
    position: relative;

}

.dropdown-wrapper ul ul {
    text-align: left;
    display: none;
}

.dropdown-wrapper ul li:hover > ul {
    display: block;
}

.dropdown-wrapper ul li ul li > a {
    text-decoration: none;
    color: inherit;
}