.app {
    height: 100vh;

    .sections {
        height: 100vh;
        margin-left: 10vw;
        width: 90vw;
        position: relative;
        scroll-snap-type: y mandatory;
        scrollbar-width: none;
        scroll-behavior: smooth;
        &::-webkit-scrollbar {
            display: none;
        }

        > *{
            scroll-snap-align: start;
        }
    }
}