.navbar {
    height: 100vh;
    width: 10vw;
    background-color: #043927;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
    position: fixed;
}

.navbar > div {
    margin-bottom: 60px;
}

.nav-button {
    height: 5%;
    width: 90%;
    color: #02160f;
    text-align: center;
    font-size: 1.3rem;
    background-color: #f0e1ab;
    border-color: honeydew;
    border-width: 2px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    border-radius: 5px;
    

    > a {
        text-decoration: none;
        color: inherit;
    }
}

.nav-button:hover {
    color: rgb(139, 127, 71);
    transition: 0.25s;
    box-shadow: 0px 0px 65px rgba(255, 196, 0, 0.4);
}