.scope {
    background-image: linear-gradient(rgb(16, 73, 8), rgb(86, 112, 16));
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    .form {
        height: 85%;
        width: 85%;
        background-color: #d8d2b8;
        border-radius: 5px;
        font-weight: bold;
        text-align: center;
    }
}

.header {
    text-align: center;
    font-size: 3rem;
    letter-spacing: 10px;
    padding: 15px;
}

.content {
    text-align: center;
    font-size: 1.2rem;
    letter-spacing: 3px;
    padding: 10px;
}

.form > img {
    width: 45%;
    height: 25%;
}